import React from "react";
import "./Products.scss";
import product1 from "../../img/products/product1.jpg";
import product2 from "../../img/products/product2.jpg";
import product3 from "../../img/products/product3.jpg";
import product4 from "../../img/products/product4.jpg";
import product5 from "../../img/products/product5.jpg";
import product6 from "../../img/products/product6.jpg";
import ProductsList from "../products_list/ProductsList.jsx";
import { motion } from "framer-motion";

let products = [
  {
    id: "1",
    img: product1,
    name: " ",
    label: "20%",
    price: 12000,
  },
  {
    id: "2",
    img: product2,
    name: "",
    label: "30%",
    price: 20000,
  },
  {
    id: "3",
    img: product3,
    name: "",
    label: "10%",
    price: 800,
  },
  {
    id: "4",
    img: product4,
    name: "",
    label: "",
    price: 600,
  },
  {
    id: "5",
    img: product5,
    name: " ",
    label: "",
    price: 600,
  },
  {
    id: "6",
    img: product6,
    name: "",
    label: "20%",
    price: 40000,
  },
];

const textVariants = {
  offscreen: {
    x: -50,
    opacity: 0,
  },
  onscreen: {
    x: 0,
    opacity: 1,
    transition: {
      type: "spring",
      bounce: 0.4,
      duration: 1,
    },
  },
};

function Products() {
  return (
    <section className="products" id="products">
      <div className="products__container">
        <motion.h2
          variants={textVariants}
          initial="offscreen"
          whileInView="onscreen"
          className="products__title"
        >
           
        </motion.h2>
        <ProductsList products={products} />
        <button className="products__load-more"> </button>
      </div>
    </section>
  );
}

export default Products;
